'use client';

import {
  TrendingUp,
  TrendingDown,
  DollarSign,
  ShoppingCart,
  Users,
  Package,
  ArrowUpRight,
  ArrowDownRight,
} from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { formatCurrency } from '@/lib/utils';

const stats = [
  {
    title: 'Günlük Satış',
    value: 2450.00,
    change: 12.5,
    trend: 'up',
    icon: DollarSign,
    color: 'text-emerald-600',
    bgColor: 'bg-emerald-100',
  },
  {
    title: 'Aylık Satış',
    value: 48750.00,
    change: 8.2,
    trend: 'up',
    icon: TrendingUp,
    color: 'text-blue-600',
    bgColor: 'bg-blue-100',
  },
  {
    title: 'Aktif Müşteri',
    value: 156,
    change: 3.1,
    trend: 'up',
    icon: Users,
    color: 'text-purple-600',
    bgColor: 'bg-purple-100',
  },
  {
    title: 'Düşük Stok',
    value: 23,
    change: -5.4,
    trend: 'down',
    icon: Package,
    color: 'text-orange-600',
    bgColor: 'bg-orange-100',
  },
];

const recentSales = [
  { id: 1, customer: 'Ahmet Yılmaz', amount: 125.50, time: '5 dk önce', items: 3 },
  { id: 2, customer: 'Mehmet Kaya', amount: 89.00, time: '12 dk önce', items: 2 },
  { id: 3, customer: 'Ayşe Demir', amount: 234.75, time: '25 dk önce', items: 5 },
  { id: 4, customer: 'Fatma Çelik', amount: 67.20, time: '1 saat önce', items: 1 },
  { id: 5, customer: 'Ali Öztürk', amount: 156.90, time: '2 saat önce', items: 4 },
];

const lowStockProducts = [
  { id: 1, name: 'Süt 1L', stock: 5, minStock: 20, unit: 'Adet' },
  { id: 2, name: 'Ekmek', stock: 8, minStock: 30, unit: 'Adet' },
  { id: 3, name: 'Peynir 500g', stock: 3, minStock: 15, unit: 'Adet' },
  { id: 4, name: 'Yumurta 30lu', stock: 2, minStock: 10, unit: 'Koli' },
];

export default function DashboardPage() {
  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Dashboard</h1>
        <p className="text-muted-foreground">
          Hoş geldiniz! İşte bugünün özeti.
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        {stats.map((stat) => (
          <Card key={stat.title}>
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                {stat.title}
              </CardTitle>
              <div className={`p-2 rounded-lg ${stat.bgColor}`}>
                <stat.icon className={`h-4 w-4 ${stat.color}`} />
              </div>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {typeof stat.value === 'number' && stat.title.includes('Satış')
                  ? formatCurrency(stat.value)
                  : stat.value}
              </div>
              <div className="flex items-center gap-1 text-xs mt-1">
                {stat.trend === 'up' ? (
                  <ArrowUpRight className="h-3 w-3 text-emerald-600" />
                ) : (
                  <ArrowDownRight className="h-3 w-3 text-red-600" />
                )}
                <span className={stat.trend === 'up' ? 'text-emerald-600' : 'text-red-600'}>
                  {Math.abs(stat.change)}%
                </span>
                <span className="text-muted-foreground">geçen aya göre</span>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Content Grid */}
      <div className="grid gap-6 md:grid-cols-2">
        {/* Recent Sales */}
        <Card>
          <CardHeader>
            <CardTitle>Son Satışlar</CardTitle>
            <CardDescription>Bugün yapılan son 5 satış</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentSales.map((sale) => (
                <div key={sale.id} className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                      <ShoppingCart className="h-4 w-4 text-primary" />
                    </div>
                    <div>
                      <p className="text-sm font-medium">{sale.customer}</p>
                      <p className="text-xs text-muted-foreground">
                        {sale.items} ürün • {sale.time}
                      </p>
                    </div>
                  </div>
                  <span className="font-semibold text-emerald-600">
                    +{formatCurrency(sale.amount)}
                  </span>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Low Stock Alert */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <span className="relative flex h-2 w-2">
                <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-orange-400 opacity-75"></span>
                <span className="relative inline-flex rounded-full h-2 w-2 bg-orange-500"></span>
              </span>
              Düşük Stok Uyarısı
            </CardTitle>
            <CardDescription>Minimum stok seviyesinin altındaki ürünler</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {lowStockProducts.map((product) => (
                <div key={product.id} className="flex items-center justify-between">
                  <div>
                    <p className="text-sm font-medium">{product.name}</p>
                    <p className="text-xs text-muted-foreground">
                      Min: {product.minStock} {product.unit}
                    </p>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-24 h-2 bg-muted rounded-full overflow-hidden">
                      <div
                        className="h-full bg-orange-500 rounded-full"
                        style={{ width: `${(product.stock / product.minStock) * 100}%` }}
                      />
                    </div>
                    <span className="text-sm font-medium text-orange-600 min-w-[60px] text-right">
                      {product.stock} {product.unit}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
